import createRenderNode from '../utils/renderNode';
import createRenderContent from '../utils/renderContent';
import { ZhiFactory } from '@ali/4ever-cangjie';
import { moFactory } from '../mo/index';

export const zhiFactory: ZhiFactory = (configs) => {
  const tbConfigs = configs?.textBackground;
  return {
    ...moFactory({}),
    renderNode: createRenderNode(tbConfigs),
    renderEditable: createRenderContent(tbConfigs),
    models: [],
  };
}
